import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params, Route } from '@angular/router';
import { SharedService } from '../../shared/services/shared.service';
import { RouterModule } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-exercise',
  templateUrl: './exercise.component.html',
  styleUrls: ['./exercise.component.css']
})
export class ExerciseComponent implements OnInit {

    busy: Promise<any>;
	
	public data = [];
    public id:any;
    constructor(
    	private _router: Router,
        private toastr: ToastrService,
        private _activateRouter: ActivatedRoute,
        private _sharedService: SharedService ) { 
    }

    ngOnInit() {
        this.id = this._activateRouter.snapshot.params['id'];
       	this.getExercise();        
    }

    getExercise() {
        this.busy = this._sharedService.getExercise(this.id).then((res: any) => {
            if(!res.error) this.data = res.data;
        },(error) => {
            if(error.headers._headers.get('content-type')[0] == "application/json; charset=utf-8") {
              this.toastr.error(error.json().msg);
            } else {
              this.toastr.error('There are some problem please try later.');
            }
        });

    }
}



