import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params, Route } from '@angular/router';
import { SharedService } from '../../shared/services/shared.service';
import { RouterModule } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-exercise-list',
  templateUrl: './exercise-list.component.html',
  styleUrls: ['./exercise-list.component.css']
})
export class ExerciseListComponent implements OnInit {

    busy: Promise<any>;
    
    public exerciseList = [];
    
    constructor(
    	private _router: Router,
        private toastr: ToastrService,
        private _sharedService: SharedService ) { 
    }

    ngOnInit() {
  		this.getExercises();        
    }

    getExercises() {
        let userID = localStorage.getItem('userID');
        this.busy = this._sharedService.getExercises(userID).then((res: any) => {
            if(!res.error) this.exerciseList = res.data;
        },(error) => {
            if(error.headers._headers.get('content-type')[0] == "application/json; charset=utf-8") {
              this.toastr.error(error.json().msg);
            } else {
              this.toastr.error('There are some problem please try later.');
            }
        });

    }
   

}



