import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { BreifComponent } from './breif/breif.component';
import { SurveyOneComponent } from './survey-one/survey-one.component';
import { SurveyTwoComponent } from './survey-two/survey-two.component';
import { ExerciseComponent } from './exercise/exercise.component';
import { ExerciseListComponent } from './exercise-list/exercise-list.component';

const routes: Routes = [
	{
		path: 'breif',
		component: BreifComponent
	},
	{
		path: 'survey-one',
		component: SurveyOneComponent
	},
	{
		path: 'survey-two',
		component: SurveyTwoComponent
	},
	{
		path: 'exercise-list',
		component: ExerciseListComponent
	},
	{
		path: 'exercise/:id',
		component: ExerciseComponent
	}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SurveyRoutingModule { }



