import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params, Route } from '@angular/router';
import { SharedService } from '../../shared/services/shared.service';
import { RouterModule } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-survey-two',
  templateUrl: './survey-two.component.html',
  styleUrls: ['./survey-two.component.css']
})
export class SurveyTwoComponent implements OnInit {

    busy: Promise<any>;

  	public selectedSurvey = [];
  	public questions      = [];
    public skip = 0
    public limit = 20;
    constructor(
    	private _router: Router,
        private toastr: ToastrService,
        private _sharedService: SharedService ) { 
    }

    ngOnInit() {
    	this.getSurvey();
    }

    getSurvey() {
      let key = 'surveyCourse2';
    	this.busy = this._sharedService.getSurvey(key).then((res: any) => {
            this.selectedSurvey = res.data && res.data.categoryAndQuestions ?  res.data.categoryAndQuestions:[];

	    	this.selectedSurvey.forEach((object)=>{
	    		object.itemsDropped.forEach((obj)=>{
	    			this.questions.push(obj);
	    		})
	    	})
        },(error) => {
            if(error.headers._headers.get('content-type')[0] == "application/json; charset=utf-8") {
              this.toastr.error(error.json().msg);
            } else {
              this.toastr.error('you are not able to login. Please try later.');
            }
        });

    }

    submitAnswer() {
      if(this.limit < this.questions.length ) {
        this.skip+= 10;
        this.limit+= 10;
        return;
      }

    	let body = {
    		userId:localStorage.getItem("userID"),
    		surveyType:'surveyCourse2',
    		userSurvey:this.questions
    	}
    	this.busy = this._sharedService.submitSurvey(body).then((res: any) => {
            this.toastr.success('Survey Successfully Submited ', ' ', { timeOut: 3000, });
            this._router.navigate(['/pages/courseinstructions']);
        },(error) => {
            if(error.headers._headers.get('content-type')[0] == "application/json; charset=utf-8") {
              this.toastr.error(error.json().msg);
            } else {
              this.toastr.error('you are not able to login. Please try later.');
            }
        });
    }

}



